,CD7BF,yBCyBA,0CAOI,mCAAA,CAAA,CAGF,wDACE,sBAAA,CACA,iBAAA,CACA,KAAA,CACA,MAAA,CAEA,iFAAA,CAEA,kCAAA,CACA,uCAAA,CACA,wBAAA,CACA,wBAAA,CACA,mBAAA,CAEA,mCAAA,CACA,SAAA,CD5CJ,yBC6BE,wDAkBI,cAAA,CACA,sCAAA,CAAA,CAKN,qBACE,8BAAA,CAEA,4BACE,sBAAA,CACA,kCAAA,CAKF,2BACE,2CAAA,CACA,gCAAA,CAKF,iCACE,SAAA,CAIJ,sBACE,8BAAA,CAEA,wBACE,0CAAA,CAMJ,yBACE,YAAA,CACA,cAAA,CACA,eAAA,CACA,mBAAA,CAEA,eAAA,CACA,iBAAA,CAEA,gBAAA,CACA,+BAAA,CD7HF,yBCmHA,yBAaI,2BAAA,CAEA,cAAA,CACA,gBAAA,CAEA,QAAA,CACA,WAAA,CAAA,CD1HJ,yBCuGA,yBAsBI,cAAA,CACA,2BAAA,CAAA,CAGF,8BACE,iBAAA,CACA,cAAA,CACA,wBAAA,CACA,sCAAA,CACA,kBAAA,CDlJJ,yBC6IE,8BAQI,YAAA,CAAA,CAMN,gCACE,YAAA,CACA,qBAAA,CACA,QAAA,CDhIF,yBC6HA,gCAMI,OAAA,CAAA,CDjKJ,yBCoKE,0HAII,cAAA,CAAA,CD5JN,yBCwJE,0HAOI,cAAA,CAAA,CAKN,4BACE,YAAA,CACA,6BAAA,CACA,QAAA,CD3JF,yBCwJA,4BAMI,yBAAA,CACA,0BAAA,CAAA,CDzJJ,yBCkJA,4BAUI,OAAA,CAAA,CD1LJ,yBC+LA,+BAEI,cAAA,CAAA,CDrLJ,yBCmLA,+BAKI,cAAA,CAAA,CAGF,kCACE,sCAAA,CACA,6BAAA,CACA,iBAAA,CAEF,sDACE,YAAA,CACA,6BAAA,CACA,QAAA,CAEA,WAAA,CACA,gBAAA,CACA,UAAA,CACA,mBAAA,CDtLJ,yBC8KE,sDAWI,OAAA,CAAA,CAGF,0DACE,UAAA,CACA,WAAA,CAEA,0BAAA,CACA,2BAAA,CACA,qBAAA,CAKN,mCACE,YAAA,CACA,sBAAA,CACA,8BAAA,CAEA,qCACE,mBAAA,CACA,kBAAA,CACA,sBAAA,CACA,QAAA,CAEA,+CAAA,CACA,8BAAA,CAEA,kBAAA,CAEA,4CACE,gCAAA,CAGF,wCACE,QAAA,CACA,sCAAA,CAGF,yCACE,yBAAA,CDjON,yBCgOI,yCAII,UAAA,CAAA","sourcesContent":["@use 'sass:math';\n@use 'breakpoints';\n\n/* FONT FACE\n=============================================================================================== */\n@font-face {\n  font-family: 'Inter';\n  font-style: normal;\n  font-weight: 100 900; // Supports a range from Thin (100) to Black (900)\n  font-display: swap; // Optional for better performance\n  src: url('../fonts/Inter-VariableFont_slnt,wght.woff2') format('truetype-variations');\n}\n\n/* FONT SIZE CALCULATIONS WITH CLAMP()\n=============================================================================================== */\n$default-min-bp: 420px;\n$default-max-bp: 1900px;\n\n@function round($number, $decimals: 0) {\n  $n: 1;\n  @if $decimals > 0 {\n    @for $i from 1 through $decimals {\n      $n: $n * 10;\n    }\n  }\n  @return math.div(math.round($number * $n), $n);\n}\n\n@function px-to-rem($px) {\n  $rems: math.div($px, 16px) * 1rem;\n  @return $rems;\n}\n\n@function fluid(\n  $min-size,\n  $max-size,\n  $min-breakpoint: $default-min-bp,\n  $max-breakpoint: $default-max-bp,\n  $unit: vw\n) {\n  $slope: math.div($max-size - $min-size, $max-breakpoint - $min-breakpoint);\n  $slope-to-unit: round($slope * 100, 2);\n  $intercept-rem: round(px-to-rem($min-size - $slope * $min-breakpoint), 2);\n  $min-size-rem: round(px-to-rem($min-size), 2);\n  $max-size-rem: round(px-to-rem($max-size), 2);\n  @return clamp(#{$min-size-rem}, #{$slope-to-unit}#{$unit} + #{$intercept-rem}, #{$max-size-rem});\n}\n\n/* CUSTOM PROPERTIES\n=============================================================================================== */\n:root {\n  // Colors\n  --primary-color: #0d0d0d;\n  --color-white: #ffffff;\n  --color-green: #22b06c;\n  --color-grey: #a5a5a5;\n\n  // Spacing\n  --160px-to-80px: #{fluid(80px, 160px)};\n  --135px-to-70px: #{fluid(70px, 135px)};\n  --120px-to-60px: #{fluid(60px, 120px)};\n  --80px-to-40px: #{fluid(40px, 80px)};\n  --70px-to-35px: #{fluid(35px, 70px)};\n  --65px-to-45px: #{fluid(45px, 65px)};\n  --40px-to-25px: #{fluid(25px, 40px)};\n  --30px-to-15px: #{fluid(15px, 30px)};\n  --25px-to-10px: #{fluid(10px, 25px)};\n  --20px-to-10px: #{fluid(10px, 20px)};\n  --15px-to-10px: #{fluid(10px, 15px)};\n  --15px-to-8px: #{fluid(8px, 15px)};\n  --12px-to-5px: #{fluid(5px, 12px)};\n  --11px-to-6px: #{fluid(6px, 11px)};\n  --7px-to-3px: #{fluid(3px, 7px)};\n  --4px-to-2px: #{fluid(2px, 4px)};\n  --single-project-skills-tag-padding1: #{fluid(5px, 15px)};\n  --single-project-skills-tag-padding2: #{fluid(10px, 25px)};\n\n  // Font sizes\n  --font-size-nav: #{fluid(20px, 28px)};\n  --font-size-text: #{fluid(22px, 34px)};\n  --font-size-h1: #{fluid(80px, 170px)};\n  --font-size-h2: #{fluid(36.5px, 90px)};\n  --font-size-skills-and-tools: #{fluid(20px, 24px)};\n  --font-size-skill-tag: #{fluid(16px, 18px)};\n  --font-size-visit-live-site: #{fluid(16px, 18px)};\n  --font-size-version: #{fluid(12px, 20px)};\n\n  // Font weights\n  --font-weight-thin: 300;\n  --font-weight-regular: 400;\n  --font-weight-medium: 510;\n  --font-weight-semibold: 550;\n\n  // Gaps\n  --flex-gap-nav: #{fluid(10px, 50px)};\n\n  // Element widths\n  --logo-width: #{fluid(100px, 149px)};\n  --homepage-description-text-width: #{fluid(600px, 890px)};\n  --homepage-project-description-text-width: #{fluid(560px, 860px)};\n  --page-not-found-width: #{fluid(550px, 820px)};\n  --profile-skill-list-text-width: #{fluid(500px, 600px)};\n  --single-project-text-width: #{fluid(560px, 960px)};\n  --h1-headline-width: #{fluid(400px, 800px)};\n\n  // Change values of variables on certain breakpoints\n  @include breakpoints.breakpoint-xxs {\n    --single-project-skills-tag-padding1: 3px;\n    --single-project-skills-tag-padding2: 6px;\n  }\n\n  @include breakpoints.breakpoint-xxs {\n    --font-size-nav: 18px;\n    --font-size-text: 18px;\n  }\n\n  @include breakpoints.breakpoint-xxs {\n    --font-size-h1: 60px;\n    --font-size-h2: 30px;\n    --font-size-skills-and-tools: 15px;\n    --font-size-skill-tag: 14px;\n    --font-size-visit-live-site: 14px;\n  }\n\n  @include breakpoints.breakpoint-xxs {\n    --logo-width: 30px;\n  }\n}\n\n/* GENERAL STYLES\n=============================================================================================== */\nhtml {\n  box-sizing: border-box;\n  min-width: 300px;\n  min-height: 100vh;\n}\n\nbody {\n  margin: 0;\n  padding: 1px 28px 0;\n\n  min-height: 100vh;\n\n  font-family: 'Inter', sans-serif;\n  letter-spacing: -0.3px;\n\n  @include breakpoints.breakpoint-xs {\n    padding: 0 15px;\n  }\n}\n\na {\n  text-decoration: none;\n  color: var(--primary-color);\n}\n\nh1,\nh2 {\n  margin: 0;\n}\n\nh1 {\n  max-width: var(--h1-headline-width);\n  margin: 0 0 var(--70px-to-35px) calc(var(--12px-to-5px) * -1);\n\n  font-size: var(--font-size-h1);\n  font-weight: var(--font-weight-regular);\n  line-height: 90%;\n\n  @include breakpoints.breakpoint-xxs {\n    max-width: 300px;\n    margin: 0 0 30px -4px;\n  }\n}\n\nh2 {\n  margin: 0 0 20px -2px;\n\n  font-size: var(--font-size-h2);\n  font-weight: var(--font-weight-medium);\n  line-height: 100%;\n}\n\nh3,\np {\n  margin: 0;\n  padding: 0;\n\n  line-height: 145%;\n  font-size: var(--font-size-text);\n}\n\nh3 {\n  display: inline-block;\n  vertical-align: top;\n  margin: var(--20px-to-10px) 0 0;\n  font-weight: var(--font-weight-medium);\n}\n\np + p {\n  margin-top: var(--40px-to-25px);\n}\n\nfigure {\n  margin: 0;\n  padding: 0;\n}\n\nimg,\nvideo {\n  display: block;\n  width: 100%;\n  max-width: 100%;\n}\n\n.hidden {\n  display: flex;\n  opacity: 0;\n  visibility: hidden;\n  pointer-events: none;\n  transition: opacity 0.2s, visibility 0.2s;\n}\n\n.hidden-xxs-half {\n  @include breakpoints.breakpoint-xxs-half {\n    display: none;\n  }\n}\n\n.visible {\n  opacity: 1;\n  visibility: visible;\n  display: flex;\n}\n\n.visible-xxs-half {\n  display: none;\n  @include breakpoints.breakpoint-xxs-half {\n    display: block;\n  }\n}\n\n/* LAZY LOADING\n=============================================================================================== */\n// Initial state of media-items with opacity set to 0\n.media {\n  opacity: 0;\n  transition: opacity 0.5s ease-out;\n}\n\n// Class to be added once the media is loaded\n.media-loaded {\n  opacity: 1;\n}\n\n/* TRANSITIONS\n=============================================================================================== */\n// Initial state of site title before transition\n.transition-in {\n  opacity: 0;\n  transform: translate3d(0, 10px, 0);\n}\n\n// Initial state of skills-and-tools div before transition\n.transition-in-opacity {\n  opacity: 0;\n}\n\n// Transition of site title\n.transition-visible {\n  opacity: 1;\n  transform: translate3d(0, 0, 0);\n  transition: opacity 0.5s ease-out 0s, transform 0.5s ease-out 0s;\n}\n\n// Transition of skills list on project pages\n.transition-visible-delayed {\n  opacity: 1;\n  transform: translate3d(0, 0, 0);\n  transition: opacity 0.5s ease-out 0.25s, transform 0.5s ease-out 0.25s;\n}\n\n// Transition of first media element on project pages\n.transition-visible-delayed-more {\n  opacity: 1;\n  transform: translate3d(0, 0, 0);\n  transition: opacity 0.5s ease-out 0.45s, transform 0.5s ease-out 0.45s;\n}\n\n/* LOGO + NAVIGATION\n=============================================================================================== */\nheader {\n  display: flex;\n  align-items: flex-end;\n  justify-content: space-between;\n\n  max-width: 1700px;\n  margin: 20px auto 0;\n\n  @include breakpoints.breakpoint-xs {\n    margin: 10px auto 0;\n  }\n\n  img {\n    max-width: var(--logo-width);\n    min-width: 80px;\n\n    @include breakpoints.breakpoint-xxs-half {\n      min-width: 31px;\n    }\n  }\n\n  nav {\n    font-weight: var(--font-weight-regular);\n\n    ul {\n      display: flex;\n      gap: var(--flex-gap-nav);\n\n      margin: 0 -15px -5px 0;\n      padding: 0 0 0 30px;\n\n      list-style: none;\n      font-size: var(--font-size-nav);\n\n      @include breakpoints.breakpoint-xxs {\n        margin: 0 -9px -3px 0;\n        padding: 0 0 0 10px;\n      }\n\n      li a {\n        padding: 8px 15px;\n        border-radius: 8px;\n\n        @include breakpoints.breakpoint-xxs {\n          padding: 4px 9px;\n        }\n\n        &:active {\n          background-color: rgb(245, 245, 245);\n        }\n      }\n    }\n  }\n}\n\n/* GENERAL STYLES FOR #CONTENT\n=============================================================================================== */\n#content {\n  display: flex;\n  flex-direction: column;\n\n  max-width: 1700px;\n  margin: var(--80px-to-40px) auto 0;\n  padding: 0;\n\n  .homepage,\n  .profile-page {\n    padding: 0 0 var(--160px-to-80px);\n  }\n}\n\n/* PROJECT LOADING ANIMATION\n=============================================================================================== */\n#loading-wrapper {\n  position: absolute;\n  top: 40%;\n  left: 50%;\n  transform: translateX(-50%);\n\n  align-items: center;\n  flex-direction: column;\n  gap: 15px;\n\n  height: 100px;\n\n  span {\n    font-size: var(--font-size-skill-tag);\n    font-weight: var(--font-weight-thin);\n    color: var(--primary-color);\n  }\n}\n\n.circle {\n  display: inline-block;\n  background-color: var(--primary-color);\n  height: 15px;\n  width: 15px;\n  border-radius: 25px;\n}\n\n#ball-1 {\n  animation-name: bounce;\n  animation-delay: 0s;\n  animation-duration: 1.4s;\n  animation-iteration-count: infinite;\n}\n#ball-3 {\n  animation-name: bounce;\n  animation-delay: 0.1s;\n  animation-duration: 1.4s;\n  animation-iteration-count: infinite;\n}\n\n@keyframes bounce {\n  0% {\n    transform: translateY(0);\n  }\n  40% {\n    transform: translateY(8px);\n  }\n  60% {\n    transform: translateY(-11px);\n  }\n  80% {\n    transform: translateY(0);\n  }\n}\n\n/* Footer\n=============================================================================================== */\nfooter {\n  display: flex;\n  align-items: baseline;\n  justify-content: space-between;\n\n  max-width: 1700px;\n  margin: 0 auto;\n  padding: var(--40px-to-25px) 0 var(--65px-to-45px);\n\n  border-top: 2px solid var(--primary-color);\n\n  a {\n    padding: 8px 15px;\n    border-radius: 8px;\n    margin-right: -15px;\n\n    @include breakpoints.breakpoint-xxs {\n      padding: 4px 9px;\n      margin-right: -9px;\n    }\n\n    &:active {\n      background-color: rgb(245, 245, 245);\n    }\n  }\n\n  @include breakpoints.breakpoint-xxs {\n    padding: 15px 0 30px;\n  }\n\n  p {\n    margin: 0;\n  }\n}\n\nbody.not-found {\n  footer {\n    display: none;\n  }\n}\n\n/* 404 page\n=============================================================================================== */\n.page-not-found {\n  max-width: var(--page-not-found-width);\n\n  .page-not-found-h1 {\n    display: flex;\n    align-items: flex-end;\n\n    max-width: 100%;\n  }\n  .description {\n    span {\n      display: inline-flex;\n    }\n  }\n\n  .arrow-right {\n    display: inline;\n    width: var(--40px-to-25px);\n    margin: 0 0 -3px 10px;\n    transform: rotate(180deg);\n\n    @include breakpoints.breakpoint-xxs {\n      width: 20px;\n    }\n  }\n\n  .page-not-found-animation {\n    width: var(--120px-to-60px);\n\n    margin: 0 0 0 var(--30px-to-15px);\n    padding: 0 0 var(--7px-to-3px);\n\n    @include breakpoints.breakpoint-xxs {\n      width: 50px;\n\n      margin: 0 0 0 12px;\n      padding: 0 0 2px;\n    }\n  }\n}\n\n/* Prevent flashes of unstyled content\n=============================================================================================== */\n// Try to hide the DOM as long as possible via inline styles\n// to prevent Flashes of unstyled text (FOUT). Then unhide the\n// DOM at the end of the CSS document:\nhtml {\n  visibility: visible;\n  opacity: 1;\n}\n","/* BREAKPOINTS\n=============================================================================================== */\n@mixin breakpoint-lg {\n  @media (max-width: 1424px) {\n    @content;\n  }\n}\n\n@mixin breakpoint-MIN-lg {\n  @media (min-width: 1200px) {\n    @content;\n  }\n}\n\n@mixin breakpoint-md {\n  @media (max-width: 1199px) {\n    @content;\n  }\n}\n\n@mixin breakpoint-MIN-md {\n  @media (min-width: 992px) {\n    @content;\n  }\n}\n\n@mixin breakpoint-sm {\n  @media (max-width: 991px) {\n    @content;\n  }\n}\n\n@mixin breakpoint-MIN-sm {\n  @media (min-width: 768px) {\n    @content;\n  }\n}\n\n@mixin breakpoint-xs {\n  @media (max-width: 767px) {\n    @content;\n  }\n}\n\n@mixin breakpoint-MIN-xs {\n  @media (min-width: 601px) {\n    @content;\n  }\n}\n\n@mixin breakpoint-xs-half {\n  @media (max-width: 600px) {\n    @content;\n  }\n}\n\n@mixin breakpoint-xxs {\n  @media (max-width: 520px) {\n    @content;\n  }\n}\n\n@mixin breakpoint-xxs-half {\n  @media (max-width: 370px) {\n    @content;\n  }\n}\n","@use 'breakpoints';\n@use 'general-styles';\n\n/* SINGLE PROJECT PAGE\n=============================================================================================== */\n.single-project {\n  .project-title-and-link {\n    h2 {\n      display: inline;\n      margin-right: var(--30px-to-15px);\n      margin-bottom: 0;\n    }\n\n    a {\n      display: inline-flex;\n      gap: 8px;\n      font-size: var(--font-size-visit-live-site);\n    }\n\n    span {\n      flex-shrink: 0;\n      margin: 6px 0 0;\n    }\n\n    img {\n      max-width: 18px;\n    }\n  }\n\n  .skills-and-tools {\n    display: flex;\n    align-items: baseline;\n    justify-content: flex-start;\n    gap: 5px;\n\n    margin: var(--15px-to-10px) 0 var(--65px-to-45px);\n    padding: var(--20px-to-10px) 0 0;\n\n    border-top: 2px solid var(--primary-color);\n\n    @include breakpoints.breakpoint-xxs {\n      margin: 11px 0 30px;\n    }\n\n    span {\n      margin-right: 5px;\n      font-size: var(--font-size-skills-and-tools);\n      font-weight: var(--font-weight-medium);\n    }\n\n    ul {\n      display: flex;\n      gap: 5px;\n      flex-wrap: wrap;\n      align-items: center;\n\n      margin: 0;\n      padding: 0;\n      list-style: none;\n\n      .skill-tag {\n        padding: var(--single-project-skills-tag-padding1) var(--single-project-skills-tag-padding2);\n\n        font-size: var(--font-size-skill-tag);\n        font-weight: var(--font-weight-regular);\n\n        border: 2px solid var(--primary-color);\n        border-radius: 50px;\n\n        @include breakpoints.breakpoint-xxs {\n          border-width: 1px;\n        }\n      }\n    }\n  }\n\n  .new,\n  .old {\n    position: relative;\n    border: 4px solid var(--color-green);\n    z-index: 0;\n\n    @include breakpoints.breakpoint-xs-half {\n      border: 3px solid var(--color-green);\n    }\n\n    &:before {\n     